import { useState } from "react";
import "./App.scss";
import Navbar from "./components/Navbar/Navbar";
import Hero from "./components/Hero";
import About from "./components/About";
import Projects from "./components/Projects"; 
import Experiences from "./components/Experiences";
import Contacts from "./components/Contacts";

function App() {
  const [activeSection, setActiveSection] = useState('hero');

  const sections = {
    hero: <Hero setActiveSection={setActiveSection} />,
    about: <About />,
    experience: <Experiences />,
    projects: <Projects />,
    contacts: <Contacts />,
  };

  return (
    <div className="GiovanniCuozzo">
      <Navbar setActiveSection={setActiveSection} activeSection={activeSection} />
      {sections[activeSection]}
      <footer className="w-full max-w-7xl mx-auto px-4 py-8 text-center text-sm text-gray-500 font-outfit">
        <p> 2026 Giovanni Cuozzo. All rights reserved.</p>
      </footer>
    </div>
  );
}

export default App;
