import React, { useState, useEffect, useCallback } from 'react';
import { motion } from 'framer-motion';
import projects from '../data/projects.json';
import { ExternalLink, Github } from 'lucide-react';
import flowWiseImage from '../assets/FlowWiseScreenshot.png';

const REPOS_TO_EXCLUDE = [
  'flow-wise-client',
  'flow-wise-server',
  'g-development.github.io',
  'g-development',
  'fastweb-dataquality-webapp',
  'Chatroom',
  'Java',
  'QlikExt-Helloworld',
  'ReactNotes',
  'QlikExt-D3js_chart',
  'Python',
  'barsPlus'
];

const Projects = () => {
  const [repos, setRepos] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  const featuredProject = projects[0];
  const otherProjects = projects.slice(1);

  const colors = ['bg-red-50', 'bg-blue-50', 'bg-green-50', 'bg-amber-50', 'bg-purple-50'];

  const fetchRepos = useCallback(async () => {
    try {
      const response = await fetch('https://api.github.com/users/G-development/repos?sort=updated&per_page=100');
      if (!response.ok) {
        throw new Error('Failed to fetch repositories');
      }
      const data = await response.json();

      const sortedRepos = data
        .filter(repo => !REPOS_TO_EXCLUDE.includes(repo.name) && !repo.archived && !repo.fork)
        .sort((a, b) => new Date(b.updated_at) - new Date(a.updated_at));

      setRepos(sortedRepos);
      setLoading(false);
    } catch (err) {
      setError(err.message);
      setLoading(false);
    }
  }, []);

  useEffect(() => {
    fetchRepos();
  }, [fetchRepos]);

  const formatDate = (dateString) => {
    const date = new Date(dateString);
    return date.toISOString().split('T')[0];
  };

  return (
    <motion.div
      className="py-16 max-w-4xl mx-auto px-4 md:px-8 lg:px-12"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.6 }}
    >
      {/* Mobile Title */}
      <motion.h1
        className="text-2xl sm:text-3xl font-bold text-center mb-12"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.6, delay: 0.1 }}
      >
        Projects
      </motion.h1>

      {/* Featured Project */}
      <motion.div
        className="mb-16"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.6, delay: 0.2 }}
      >
        <div className="bg-green-950 text-white rounded-3xl overflow-hidden">
          <div className="flex flex-col-reverse lg:flex-row">
            <div className="p-8 lg:p-12 lg:w-1/2">
              <div className="flex items-center gap-3 mb-4">
                <h2 className="text-2xl sm:text-3xl font-bold">{featuredProject.name}</h2>
                <span className="bg-green-500 text-white text-xs font-semibold px-3 py-1 rounded-full">
                  {featuredProject.status}
                </span>
              </div>
              <p className="mb-8 text-sm sm:text-base text-blue-100">
                {featuredProject.description}<br /><br />
                {featuredProject.features ? featuredProject.features.join('. ') : ''}<br /><br />
                Technologies: {featuredProject.technologies.join(', ')}.
              </p>
              <div className="flex flex-wrap gap-4">
                {featuredProject.links?.demo && (
                  <a
                    href={featuredProject.links.demo}
                    className="inline-flex items-center justify-center gap-2 bg-primary text-white px-8 py-3 rounded-full hover:bg-primary-dark transition-colors duration-300"
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    <ExternalLink className="w-5 h-5" />
                    View Project
                  </a>
                )}  
                {featuredProject.links?.github && (
                  <a
                    href={featuredProject.links.github}
                    className="inline-flex items-center justify-center gap-2 bg-white border border-gray-200 text-gray-800 px-8 py-3 rounded-full hover:bg-gray-50 transition-colors duration-300"
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    <Github className="w-5 h-5" />
                    View on GitHub
                  </a>
                )}
              </div>
            </div>
            <div className="lg:w-1/2 bg-gradient-to-br from-green-900 to-green-950 p-8 flex items-center justify-center">
              <img src={flowWiseImage} alt="Flow-Wise Screenshot" className="max-w-full max-h-96 object-contain rounded-lg" />
            </div>
          </div>
        </div>
      </motion.div>

      {/* GitHub Repositories List */}
      <motion.div
        className="mb-16"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.6, delay: 0.3 }}
      >
        <h2 className="text-2xl sm:text-3xl font-bold text-center mb-8">GitHub repositories</h2>
        <div className="bg-gray-50 rounded-2xl p-6">
          {loading && (
            <div className="text-center py-8 text-gray-600">
              Loading repositories...
            </div>
          )}

          {error && (
            <div className="text-center py-8 text-red-600">
              Error loading repositories: {error}
            </div>
          )}

          {!loading && !error && (
            <ul className="space-y-1">
              {repos.map((repo, index) => (
                <li key={repo.id} className="flex items-center py-3 border-b border-gray-200 last:border-b-0 hover:bg-gray-100 transition-colors duration-200 rounded">
                  <div className="flex items-center gap-3 flex-1">
                    <span className="bg-gray-200 text-gray-700 font-bold px-3 py-1 rounded-md text-sm min-w-[40px] text-center">
                      {index + 1}
                    </span>
                    <span className="font-medium text-gray-900 flex-1">{repo.name}</span>
                  </div>
                  <div className="flex items-center gap-4">
                    <span className="text-sm text-gray-500 hidden sm:block">
                      {formatDate(repo.updated_at)}
                    </span>
                    <a
                      href={repo.html_url}
                      target="_blank"
                      rel="noopener noreferrer"
                      className="text-gray-600 hover:text-primary hover:scale-110 transition-all duration-200"
                      aria-label={`View ${repo.name} on GitHub`}
                    >
                      <Github className="w-5 h-5" />
                    </a>
                  </div>
                </li>
              ))}
            </ul>
          )}
        </div>
      </motion.div>

      {/* Other Projects Grid */}
      <motion.div
        className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ duration: 0.6, delay: 0.4 }}
      >
        {otherProjects.map((project, index) => (
          <motion.div
            key={index}
            className={`${colors[index % colors.length]} rounded-2xl p-6 h-full`}
            whileHover={{ scale: 1.02 }}
            transition={{ duration: 0.3 }}
          >
            <h3 className="text-xl font-semibold mb-2">{project.name}</h3>
            <p className="text-gray-700 mb-4">{project.description}</p>
            <div className="flex flex-wrap gap-2 mb-4">
              {project.technologies.slice(0, 3).map((tech, i) => (
                <span key={i} className="bg-white bg-opacity-50 text-gray-800 px-2 py-1 rounded text-sm">
                  {tech}
                </span>
              ))}
            </div>
            <div className="flex gap-4">
              {project.links?.demo && (
                <a
                  href={project.links.demo}
                  className="inline-flex items-center text-gray-800 font-medium hover:text-gray-600 transition-colors"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  View Project <ExternalLink className="w-4 h-4 ml-1" />
                </a>
              )}
              {project.links?.github && (
                <a
                  href={project.links.github}
                  className="inline-flex items-center text-gray-800 font-medium hover:text-gray-600 transition-colors"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  GitHub <Github className="w-4 h-4 ml-1" />
                </a>
              )}
            </div>
          </motion.div>
        ))}
      </motion.div>

    </motion.div>
  );
};

export default Projects;