import { motion } from 'framer-motion';
import experience from '../data/experience.json';
import { MapPin, Calendar } from 'lucide-react';

const MONTH_NAMES = [
  'Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun',
  'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec',
];

const formatDate = (ym) => {
  if (!ym) return 'Today';
  const [y, m] = ym.split('-');
  return m ? `${MONTH_NAMES[parseInt(m, 10) - 1]} ${y}` : ym;
};

const Experiences = () => {
  return (
    <motion.div 
      className="py-16 max-w-5xl mx-auto px-4 md:px-8 lg:px-12"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.6 }}
    >
      <motion.h1 
        className="text-2xl sm:text-3xl font-bold text-center mb-16"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.6, delay: 0.2 }}
      >
        Experience
      </motion.h1>
      
      <div className="relative">
        {/* Timeline vertical line */}
        <div className="absolute left-4 md:left-8 top-0 bottom-0 w-0.5 bg-gray-200"></div>
        
        <div className="space-y-8 md:space-y-12">
          {experience.map((exp, index) => (
            <motion.div
              key={index}
              className="relative pl-10 md:pl-24"
              initial={{ opacity: 0, x: -20 }}
              whileInView={{ opacity: 1, x: 0 }}
              transition={{ duration: 0.6, delay: index * 0.1 }}
              viewport={{ once: true }}
            >
              {/* Bullet point on timeline */}
              <div className="absolute left-[13px] md:left-[26px] top-1 w-2.5 h-2.5 md:w-3 md:h-3 bg-primary rounded-full border-2 border-white shadow"></div>
              
              {/* Date and Location on timeline */}
              <div className="mb-3">
                <div className="flex items-center gap-1.5 md:gap-2 text-xs md:text-sm font-semibold text-gray-900">
                  <Calendar className="w-3.5 h-3.5 md:w-4 md:h-4 text-gray-500 hidden sm:block" />
                  <span>{formatDate(exp.startDate)} - {exp.endDate ? formatDate(exp.endDate) : 'Present'}</span>
                </div>
                <div className="flex items-center gap-1.5 md:gap-2 text-xs md:text-sm text-gray-600 mt-1">
                  <MapPin className="w-3.5 h-3.5 md:w-4 md:h-4 text-gray-500 hidden sm:block" />
                  <span>{exp.location}</span>
                </div>
              </div>

              {/* Experience card */}
              <div className="bg-white border border-gray-200 rounded-lg p-4 md:p-6 shadow-sm hover:shadow-md transition-shadow duration-300">
                <div className="mb-3 md:mb-4">
                  <h3 className="text-base sm:text-lg md:text-xl font-semibold text-gray-900 mb-1">{exp.role}</h3>
                  <h4 className="text-sm sm:text-base md:text-lg text-primary font-medium">{exp.company}</h4>
                </div>
                
                <p className="text-xs sm:text-sm md:text-base text-gray-700 mb-3 md:mb-4 leading-relaxed">{exp.description}</p>
                
                <ul className="list-disc list-inside text-xs sm:text-sm text-gray-600 mb-3 md:mb-4 space-y-1">
                  {exp.responsibilities.map((resp, i) => (
                    <li key={i}>{resp}</li>
                  ))}
                </ul>
                
                <div className="flex flex-wrap gap-1.5 md:gap-2">
                  {exp.technologies.map((tech, i) => (
                    <span key={i} className="bg-gray-100 text-gray-800 px-2 md:px-3 py-1 rounded-full text-xs md:text-sm font-medium">
                      {tech}
                    </span>
                  ))}
                </div>
              </div>
            </motion.div>
          ))}
        </div>
      </div>
    </motion.div>
  );
};

export default Experiences;