import { useState } from "react";
import profile from "../../data/profile.json";
import { User, Sparkles, Boxes, SendHorizontal, Github, Linkedin } from "lucide-react";

const links = [
  { name: "About", icon: User, link: "about" },
  { name: "Experience", icon: Sparkles, link: "experience" },
  { name: "Projects", icon: Boxes, link: "projects" },
  { name: "Contacts", icon: SendHorizontal, link: "contacts" }
];

const Navbar = ({ setActiveSection, activeSection }) => {
  const [open, setOpen] = useState(false);

  const handleLinkClick = (link) => {
    setActiveSection(link);
    setOpen(false);
  };

  return (
    <nav className="navbar sticky top-0 flex items-center w-full bg-white border-b px-4 md:px-8 lg:px-12 py-4 z-50">
      <div className="flex-1 md:flex-none">
        <button onClick={() => handleLinkClick('hero')} className="logo text-xl font-bold hover:opacity-80 hover:text-primary-dark transition-all duration-300 ease-in-out">
          <span className="md:hidden">GC</span>
          <span className="hidden md:inline">Giovanni Cuozzo</span>
        </button>
      </div>

      <div className="hidden md:flex flex-1 justify-center">
        <div className="bg-gray-100 rounded-full p-1 flex items-center justify-center gap-1 md:gap-2">
          {links.map((l) => (
            <li key={l.name} className="flex items-center list-none">
              <button
                type="button"
                onClick={() => handleLinkClick(l.link)}
                aria-current={activeSection === l.link ? 'page' : undefined}
                className={`flex items-center gap-2 px-3 py-2 rounded-full transition-all duration-300 ease-in-out ${activeSection === l.link
                  ? 'bg-white text-black font-semibold shadow-md'
                  : 'text-gray-600 hover:text-black'
                  }`}
              >
                <l.icon className="w-5 h-5" />
                <span>{l.name}</span>
              </button>
            </li>
          ))}
        </div>
      </div>

      <div className="flex-1 md:flex-none flex justify-end">
        <ul className="flex gap-2">
          <li>
            <a
              href={profile.contacts?.linkedin}
              target="_blank"
              rel="noopener noreferrer"
              className="flex items-center justify-center text-black hover:text-primary-dark transition-colors duration-300 ease-in-out"
            >
              <Linkedin className="w-6 h-6 md:w-7 md:h-7" />
            </a>
          </li>
          <li>
            <a
              href={profile.contacts?.github}
              target="_blank"
              rel="noopener noreferrer"
              className="flex items-center justify-center text-black hover:text-primary-dark transition-colors duration-300 ease-in-out"
            >
              <Github className="w-6 h-6 md:w-7 md:h-7" />
            </a>
          </li>
        </ul>
      </div>

      <button
        className="md:hidden flex flex-col justify-center items-center w-6 h-6 ml-4"
        onClick={() => setOpen(!open)}
        aria-label="Toggle menu"
      >
        <span className={`block w-5 h-0.5 bg-black transition-transform duration-300 ${open ? "rotate-45 translate-y-1" : "-translate-y-1"}`} />
        <span className={`block w-5 h-0.5 bg-black transition-opacity duration-300 ${open ? "opacity-0" : "opacity-100"}`} />
        <span className={`block w-5 h-0.5 bg-black transition-transform duration-300 ${open ? "-rotate-45 -translate-y-1" : "translate-y-1"}`} />
      </button>

      <div className={`absolute top-full left-0 w-full bg-white border-b md:hidden transition-all duration-300 ease-in-out ${open ? 'opacity-100 translate-y-0 pointer-events-auto' : 'opacity-0 -translate-y-2 pointer-events-none'}`}>
        <ul className="flex flex-col p-4 gap-2">
          {links.map((l) => (
            <li key={l.name}>
              <button
                type="button"
                onClick={() => handleLinkClick(l.link)}
                aria-current={activeSection === l.link ? 'page' : undefined}
                className={`flex items-center gap-2 px-3 py-2 rounded-lg transition-all ${activeSection === l.link
                  ? 'bg-gray-100 text-black font-semibold'
                  : 'text-gray-600 hover:text-black'
                  }`}
              >
                <l.icon className="w-5 h-5" />
                <span>{l.name}</span>
              </button>
            </li>
          ))}
        </ul>
      </div>
    </nav>
  );
};

export default Navbar;